@charset "UTF-8";

@use './modules/index';
/*
 font-family: 'Lato', sans-serif;
 font-family: 'Noto Sans JP', sans-serif;
*/

/*common*/
.comTitle{
    .comCont{
        text-align: center;
        font: normal normal bold 36px/14px Yu Gothic;
        letter-spacing: 3.6px;
        color: #333333;
        opacity: 1;
    }
    .comBar{
        position: block;
        margin: 20px auto;
        width: 100px;
        height: 0px;
        border: 3px solid #333333;
        opacity: 1;
    }
}
.bl-catchimg{
    width: 120px;
    height: 120px;
    background-color: #ccc; 
    border-radius: 50%;    
    opacity: 1;
}
.bl-catchCardCont{
    width: 100%;
    height: 142px;
    background: #E9F1FB 0% 0% no-repeat padding-box;
    border-radius: 15px;
    opacity: 1;
    align-items: center;
    .bl-catchCardContM{
        padding:20px ;
        font: normal normal normal 16px/27px Yu Gothic;
        letter-spacing: 1.6px;
        color: #333333;    }
}

body{
    font-family: '游ゴシック体', 'YuGothic',sans-serif;
    box-sizing: border-box;         /*ブロックがありパディングがありボーダまでを同じサイズですよ*/
    .container{
        background-image:url('../img/24719912_l.jpg') ;
        background-size: cover;
        background-repeat: no-repeat;
        max-width: 1280px;
        margin: 0 auto;
    }

    /* header */
    .header{
        background-color: #1c0da141;
        border: 1px solid #D3D3D3;
        margin-bottom: 20px;
        .bl-header{
            margin: 0 auto;
            width: 100%;
            .bl-headerMenu{
                display: flex;
                justify-content: space-between;
                margin-left: 10px;
                .bl-headerLogo{
                    img{
                        vertical-align: middle;
                        width: 57px;
                        // height: 73px;
                        box-shadow: 0px 3px 6px #00000029;
                        opacity: 1;
                    }
                    h1{
                        display: inline-block;
                        vertical-align: middle;
                        width: 210px;
                        margin:0 0 0 10px;
                        font-size: 22px;
                        letter-spacing: 1.1px;
                        color: #FFFFFF;
                        @media(max-width: 1000px) {
                            width: 150px;
                            font-size: 18px;

                        }
                    }
                }
                .bl-headerNav{
                    list-style-type: none;
                    text-align: center;
                    margin:15px 10px 15px 10px;
                    .bl-headerNavLists{
                        display: inline-block;
                        width: 210px;
                        padding: 10px;
                        background: #FFFF;
                        border-radius: 10px;
                        a{
                            color: #000000;
                            font-weight: bold;
                            font-size: 22px;
                            letter-spacing: 1.1px;
                            text-decoration: none;
                        }
                        &:hover{
                            background: #060506FA;
                            a{
                                color: #fff;
                            font-weight: bold;
                            }
                        }
                    }
                }
            }
            .bl-humbergarMenu{
                display: none;
                position: absolute;
                right: 0px;
                top: 0px;
                background: #FF2A2A 0% 0% no-repeat padding-box;
                .openbtn4{
                    position: relative;/*ボタン内側の基点となるためrelativeを指定*/
                    cursor: pointer;
                    width: 100px;
                    height:100px;
                    border-radius: 5px;
                    span{
                        display: inline-block;
                        transition: all .4s;/*アニメーションの設定*/
                        position: absolute;
                        left: 25px;
                        height: 3px;
                        background: #FFFF;
                        width: 50px;
                        &:nth-of-type(1){
                            top:36px; 
                        }
                        &:nth-of-type(2){
                            top:50px; 
                        }
                        &:nth-of-type(3){
                            top:64px; 
                        }
    
                    }
                }
                @media screen and (max-width: 520px) {
                        display: block;
                }
            }
        }
    }
    /* main */
    .main{
        /*project*/
        width: 100%;
        opacity: 1;
        .topMenu{
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
            padding: 0 50px;
            .topMenuCard{
                // margin: 64px 0 0 32px;
                width: 560px;
                padding: 20px;
                /* UI Properties */
                border: 1px solid var(--unnamed-color-707070);
                background: transparent  0% 0% ;
                box-shadow: inset 0px 3px 6px #00000029, 0px 3px 6px #101074F0;
                border: 1px solid #707070;
                border-radius: 20px;
                opacity: 1;
                backdrop-filter: blur(30px);
                -webkit-backdrop-filter: blur(30px);
                position: relative;
                .topMenuListTitle{
                    // position: absolute;
                    margin-bottom:10px;
                    text-decoration: underline;
                    font-size:32px;
                    letter-spacing: 1.6px;
                    color: #FFFEFE;
                    text-transform: uppercase;
                }
                .topMenuList{
                    // position: absolute;
                    top: 65px;
                    left: 10px;
                    li{
                        display: flex;
                        align-items: flex-start;
                        margin-top: 10px;
                        .ListImgNo{
                            width: 15px;
                            // height: 32px;
                        }
                        .ListImgCnt{
                            margin-left: 10px;
                            width: 94px;
                            // height: 66px;
                        }
                        .ListTitle{
                            margin-left: 10px;
                            font-weight: bold;
                            color: #ccc;
                            font-size: 20px;
                        }
                        .ListDate{
                            margin-left: 10px;
                            font-weight: bold;
                            color: #ccc;
                            font-size: 20px;
                        }
                    }
                }
                &:nth-of-type(2){
                    margin-top: 200px;
                }
            }
        }
    }
    .footer{
        margin-top: 119px;
        background-color: #E8E8E8;
        .footer-box{
            max-width: 860px;
            padding: 60px 0 0 0;
            margin: 0 auto;
            display: flex;
            justify-content: space-between;
            ul{
                list-style: none;
                font-size:16px;
                font-weight: bold;
                letter-spacing: 1.6px;
                color: #333333;
                li{
                    font-weight: normal;
                    padding: 3px 0 0 10px;
                    &:nth-child(1){
                        padding: 10px 0 0px 10px;
                    }
                    a{
                        text-decoration: none;
                    }
                }
            }
        }
        .copyright{
            width: 100%;
            display: flex;
            justify-content: center;
            padding: 60px 0 20px 0;
            font-size: 10px;
            letter-spacing: 1px;
            color: #333333;
        }
    }
}